import React from "react";
import { Form, Input } from "antd";

const dummy = {
  isLoggedIn:true
}

//메인화면
const Home = () => {
  return (
    <div>
      {dummy.isLoggedIn && <Form encType = "multipart/form-data">
        <Input.tex></Input.tex>
        </Form>}
    </div>
  );
};

export default Home;
